/** @format */

import { useState, useEffect } from "react";
import { createContainer } from "unstated-next";
import { version } from "./Version";
import {
  getServerVersionURL,
  getHolidaysURL,
  getConfigURL,
  getFindsV2URL,
  getSunURL,
  getWxURL,
} from "./Server";
import axios from "axios";
import { useDays } from "./Days";
import { Themes } from "../theme/Themes";
import { useCustomTheme } from "../theme/useCustomTheme";
import * as df from "./dates";

export const useStore = () => {
  const defaultDays = 21;

  const [theme, setTheme, componentMounted] = useCustomTheme(Themes.Green);
  const [toDoOpen, setToDoOpen] = useState(false);
  const [toDoWeek, setToDoWeek] = useState({ dates: [], goal: 0, number: 0 });
  const [detailIsOpen, setDetailIsOpen] = useState(false);
  const [clickedDate, setClickedDate] = useState("");
  const [days, setDays] = useDays(defaultDays);
  const [dataLoaded, setDataLoaded] = useState(false);
  const [findData, setFindData] = useState({});
  const [holidays, setHolidays] = useState({});
  const [serverVersion, setServerVersion] = useState("0.0.0");
  const [config, setConfig] = useState({
    pq_time: "",
    update_time: "",
    goal: 4,
  });
  const [position, setPosition] = useState({
    status: "fail",
    latitude: 0,
    longitude: 0,
  });
  const [appVersion, setAppVersion] = useState(version);
  const [sunTimes, setSunTimes] = useState({
    results: {
      sunrise: "",
      sunset: "",
    },
    status: "fail",
  });
  const [sunTimesLoaded, setSunTimesLoaded] = useState(false);

  const [weatherIsLoaded, setWeatherIsLoaded] = useState(false);
  const [weather, setWeather] = useState({});

  useEffect(() => {
    // get the server version
    axios.get(getServerVersionURL()).then((res) => {
      setServerVersion(res.data.version);
    });
  }, []);

  useEffect(() => {
    // get the position if possible
    if ("geolocation" in navigator) {
      navigator.geolocation.getCurrentPosition((pos) => {
        setPosition({
          status: "ok",
          latitude: pos.coords.latitude,
          longitude: pos.coords.longitude,
        });
      });
    }
  }, []);

  useEffect(() => {
    // get the sunrise/sunset times
    const sunLoc = `lat=${position.latitude}&lon=${position.longitude}`;
    const qDT = df.getFullDate(clickedDate.offset);

    const sunURL = `${getSunURL()}&${sunLoc}&date=${qDT}`;

    axios.get(sunURL).then((response) => {
      // console.log("sun times:");
      // console.log(response.data);
      setSunTimes(response.data);
      setSunTimesLoaded(true);
    });
  }, [position, clickedDate.offset]);

  useEffect(() => {
    // get the holidays
    axios.get(getHolidaysURL()).then((res) => {
      setHolidays(res.data);
    });
  }, []);

  // get the configuration info
  useEffect(() => {
    axios.get(getConfigURL()).then((res) => {
      setConfig(res.data);
      // console.log(res.data);
    });
  }, []);

  // get the find data
  useEffect(() => {
    // get the find data
    axios.get(getFindsV2URL()).then((res) => {
      setFindData(res.data);
      setDataLoaded(true);
    });
  }, []);

  useEffect(() => {
    const location = `loc=${position.latitude},${position.longitude}`;
    const qDT = df.getFullDate(clickedDate.offset);

    // getWxURL: https://..../api/weather?key=...
    const wxURL = `${getWxURL()}&${location}&dt=${qDT}`;

    axios.get(wxURL).then((response) => {
      setWeather(response.data);
      setWeatherIsLoaded(true);
    });
  }, [position, clickedDate.offset]);

  return {
    dataLoaded,
    findData,
    setFindData,
    holidays,
    serverVersion,
    config,
    position,
    appVersion,
    setAppVersion,
    days,
    setDays,
    theme,
    setTheme,
    componentMounted,
    toDoOpen,
    setToDoOpen,
    toDoWeek,
    setToDoWeek,
    detailIsOpen,
    setDetailIsOpen,
    clickedDate,
    setClickedDate,
    sunTimes,
    sunTimesLoaded,
    weather,
    weatherIsLoaded,
  };
};

export const StoreContainer = createContainer(useStore);
