/** @format */

import CssBaseline from "@material-ui/core/CssBaseline";
import { MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import "fontsource-roboto";
import DayDetail from "./components/DayDetail";
import FindGrid from "./components/FindGrid";
import NextWeek from "./components/NextWeek";
import { StoreContainer } from "./infrastructure/store";
import { DataEngine } from "./infrastructure/DataEngine";

const App = () => {
  const store = StoreContainer.useContainer();

  if (!store.componentMounted || !store.dataLoaded) {
    return <div>Loading...</div>;
  }

  return (
    <>
      <DataEngine.Provider>
        <MuiThemeProvider theme={store.theme}>
          <CssBaseline />
          <FindGrid />
          <Typography
            align="right"
            variant="subtitle2"
            style={{ paddingRight: "10px" }}>
            {store.appVersion}/{store.serverVersion}
          </Typography>
          <DayDetail />
          <NextWeek />
        </MuiThemeProvider>
      </DataEngine.Provider>
    </>
  );
};

export default App;
