/** @format */

import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles, withTheme } from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import { ThemeArray } from "../theme/Themes";
import PaletteIcon from "@material-ui/icons/Palette";
import DateRangeIcon from "@material-ui/icons/DateRange";
import { DayChoices } from "../infrastructure/Days";
import ViewWeekRoundedIcon from "@material-ui/icons/ViewWeekRounded";
import { StoreContainer } from "../infrastructure/store";
import { getCurrentWeek } from "../infrastructure/dates";

const FindCountHeader = (props) => {
  const store = StoreContainer.useContainer();

  const small = useMediaQuery(props.theme.breakpoints.down("sm"));
  const titleText = small ? "Finds" : "Cache Find Counts";

  const useStyles = makeStyles(() => ({
    text: {
      flex: 1,
      color: props.theme.palette.primary.headerText,
    },
    spacer: {
      height: "10px",
    },
    formControl: {
      paddingRight: "10px",
      paddingLeft: "5px",
    },
    selectText: {
      color: props.theme.palette.primary.headerText,
    },
    nextText: {
      color: props.theme.palette.primary.headerText,

      paddingLeft: "7px",
      paddingRight: "7px",
    },
    barText: {
      color: props.theme.palette.primary.headerText,

      paddingLeft: "5px",
      paddingRight: "15px",
    },
    icon: {
      color: props.theme.palette.primary.headerText,
    },
    weekIcon: {
      paddingRight: "10px",
      cursor: "pointer",
    },
  }));
  const classes = useStyles();

  const nextText = () => {
    if (small) {
      return "";
    }

    return <Typography className={classes.nextText}>Next:</Typography>;
  };

  const changeTheme = (event) => {
    store.setTheme(event.target.value);
  };

  const handleWeek = () => {
    const week = getCurrentWeek(store.findData);

    store.setToDoWeek({ ...week, goal: store.config.goal });
    store.setToDoOpen(true);
  };

  const changeDays = (event) => {
    store.setDays(event.target.value);
  };

  return (
    <>
      <AppBar position="fixed">
        <Toolbar>
          <Typography className={classes.text}>{titleText}</Typography>
          <ViewWeekRoundedIcon
            className={`${classes.icon} ${classes.weekIcon}`}
            onClick={handleWeek}
          />
          <Typography className={classes.barText}>|</Typography>

          <DateRangeIcon className={classes.icon} />
          {nextText()}
          <FormControl className={classes.formControl}>
            <Select
              disableUnderline
              classes={{ icon: classes.selectText, root: classes.selectText }}
              labelId="rangeLabel"
              id="rangeSelect"
              value={store.days}
              onChange={changeDays}>
              {DayChoices.map((d) => {
                return (
                  <MenuItem key={d} value={d}>
                    {`${d} days`}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
          <Typography className={classes.barText}>|</Typography>
          <PaletteIcon className={classes.icon} />
          <FormControl className={classes.formControl}>
            <Select
              disableUnderline
              classes={{ icon: classes.selectText, root: classes.selectText }}
              labelId="themeLabel"
              id="themeSelect"
              value={props.theme}
              onChange={changeTheme}>
              {ThemeArray.map((th) => {
                return (
                  <MenuItem key={th.palette.name} value={th}>
                    {th.palette.name}
                  </MenuItem>
                );
              })}
            </Select>
          </FormControl>
        </Toolbar>
      </AppBar>
      <div className={classes.spacer}></div>
    </>
  );
};

export default withTheme(FindCountHeader);
