/** @format */

// contains methods for retrieving and formatting data

import { createContainer } from "unstated-next";
import { StoreContainer } from "./store";
import * as df from "./dates";

export const useDataEngine = () => {
  const store = StoreContainer.useContainer();

  // get the goal from configuration
  const getGoal = () => {
    return store.config.goal;
  };

  // get the index of a date, not exported
  const getDateIndex = (date) => {
    if (date in store.findData.dateIndex) {
      return store.findData.dateIndex[date];
    }
  };

  // returns the total number of finds for the given date
  const getTotalFinds = (date) => {
    const index = getDateIndex(date);

    if (index === "") {
      return -1;
    }

    return store.findData.dates[index].total_finds;
  };

  // returns the number of lab finds for the given date
  const getLabFinds = (date) => {
    const index = getDateIndex(date);

    if (index === "") {
      return -1;
    }

    return store.findData.dates[index].lab_finds;
  };

  // returns the number of normal finds for the given date
  const getNormalFinds = (date) => {
    const index = getDateIndex(date);

    if (index === "") {
      return -1;
    }

    return store.findData.dates[index].norm_finds;
  };

  // returns the number of find required for the given date.
  // if the current count exceeds the goal, zero is returned
  const getNumToFind = (date) => {
    const f = getGoal() - getTotalFinds(date);
    return f < 0 ? "0" : f;
  };

  // convenience function to return find data for a given date
  const getDayFindData = (date) => {
    if (date) {
      return {
        numToFind: getNumToFind(date),
        totalFinds: getTotalFinds(date),
        normalFinds: getNormalFinds(date),
        labFinds: getLabFinds(date),
        goal: getGoal(),
      };
    } else {
      return {
        numToFind: 0,
        totalFinds: 0,
        normalFinds: 0,
        labFinds: 0,
        goal: 0,
      };
    }
  };

  // returns the find count data and summary for the given number of days
  // days: number of days to get data for
  const getFindCountData = (days) => {
    const data = df.getNDays(days, store.findData);
    const summaryInfo = {
      generated: new Date(store.config.pq_time).toLocaleString(),
      goal: getGoal(),
      next7days: df.getReqFindsNextWeek(data.dates, getGoal()),
    };

    return {
      data: data,
      summary: summaryInfo,
    };
  };

  return {
    getGoal,
    getTotalFinds,
    getLabFinds,
    getNormalFinds,
    getNumToFind,
    getDayFindData,
    getFindCountData,
  };
};

export const DataEngine = createContainer(useDataEngine);
