/** @format */

import React from "react";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";
import Header from "./Header";
import Week from "./Week";
import { getWeeks, getShortDayNames } from "../infrastructure/ComposeWeeks";
import SummaryCard from "./SummaryCard";
import { StoreContainer } from "../infrastructure/store";
import { DataEngine } from "../infrastructure/DataEngine";

const useStyles = makeStyles((/*theme*/) => ({
  g7root: {
    flexGrow: 1,
    padding: "10px",
  },
  summary: {
    paddingBottom: "16px",
  },
}));

const Grid7 = () => {
  const store = StoreContainer.useContainer();
  const dataEngine = DataEngine.useContainer();

  // const fcd = getFindCountData(store.days, store.findData);
  const fcd = dataEngine.getFindCountData(store.days);
  const weeks = getWeeks({ ...fcd.data, goal: dataEngine.getGoal() });

  const classes = useStyles();

  let weekNum = 0;

  return (
    <>
      <Grid container className={classes.g7root} spacing={0}>
        <Grid item xs={12} className={classes.summary}>
          <SummaryCard summary={fcd.summary} />
        </Grid>
        <Header values={getShortDayNames()} />
        {weeks.map((week) => (
          <Week values={week} key={weekNum++} />
        ))}
      </Grid>
    </>
  );
};

export default Grid7;
