/** @format */

// convenience file for palette access

import GreenPalette from "../Palettes/GreenPalette";
import TealPalette from "../Palettes/TealPalette";
import DarkPalette from "../Palettes/DarkPalette";
import GrayPalette from "../Palettes/GrayPalette";
import BluePalette from "../Palettes/BluePalette";

import { generateTheme } from "./GenerateTheme";

// all available themes
export const Themes = {
  Blue: generateTheme(BluePalette),
  Green: generateTheme(GreenPalette),
  Teal: generateTheme(TealPalette),
  Dark: generateTheme(DarkPalette),
  Gray: generateTheme(GrayPalette),
};

// the array of theme names
export const ThemeNames = Object.keys(Themes).map(
  (key) => Themes[key].palette.name
);

// themes as an array
export const ThemeArray = Object.keys(Themes).map((key) => Themes[key]);

export default Themes;
